<template>
  <div class="box">
    <div class="new-business">
      <router-link :to="{path:'/business-add',query:{hotelId:$route.query.hotelId}}"> + 添加二房东</router-link>
    </div>
    <div class="business-group">
      <div
        class="business-item"
        v-for="item in businessList"
        :key="item.id"
      >
        <div class="business-title">
          <router-link :to="{path:'/business-details',query:{hotelId:$route.query.hotelId,cooperationName:item.cooperationName,cooperationId:item.id,contact:item.contact,phone:item.phone}}">{{item.cooperationName}}
            <van-icon
              name="arrow"
              style="vertical-align: middle;"
            />
          </router-link>
        </div>
        <div class="business-main business-bet">
          <div class="bm-left">
            <div class="bm-user">联系人：<span>{{item.contact}} {{item.phone}}</span></div>
            <div class="bm-room">房间数：<span>{{item.roomNum}}间</span></div>
          </div>
        </div>
        <div class="btn-group">
          <div
            class="business-btn cell-btn-none"
            @click="routerGo('/stateToday_two',item)"
          >查看房间</div>
          <div
            class="business-btn cell-btn-color"
            @click="routerGo('/check_bill_two',item)"
          >查看账单</div>
        </div>
      </div>
      <div style="height:30px;width:100%;"></div>
    </div>
  </div>
</template>

<script>
import { GetBusinessCooperationByhotel } from '@/api/api_business';

export default {
  name: 'index',
  data() {
    return {
      businessList: []
    };
  },
  created() {
    this.$store.commit('setHeader', '二房东');
    this.getBusinessCoop(this.$route.query.hotelId);
  },
  methods: {
    //查询单位协议
    getBusinessCoop(hotelId) {
      GetBusinessCooperationByhotel({ hotelId: hotelId }).done((res) => {
        this.businessList = res.data; //[...res.data,...res.data,...res.data,...res.data,...res.data]
      });
    },

    //路径跳转
    routerGo(link, item) {
      if (link == '/stateToday_two') {
        this.$router.push({
          path: link,
          query: {
            cooperId: item.id,
            hotelId: this.$route.query.hotelId,
            cooperationName: item.cooperationName,
            contact: item.contact,
            phone: item.phone
          }
        });
      } else {
        this.$router.push({
          path: link,
          query: {
            cooperationName: item.cooperationName,
            cooperId: item.id,
            hotelId: this.$route.query.hotelId
          }
        });
      }
    }
  }
};
</script>

<style scoped>
.box {
    height: calc(100vh - 8%);
}
.new-business {
    height: 2.4rem;
    background: #fff;
    font-size: 0.9rem;
    line-height: 2.4rem;
    color: #000;
    margin: 0.2rem 0;
}
.business-group {
    height: calc(100vh - 8% - 2.8rem);
    overflow-y: auto;
}
.business-item {
    background: #fff;
    padding: 0.6rem 0.6rem;
    margin: 0.25rem 0;
}
.business-title {
    font-weight: 500;
    font-size: 0.9rem;
    text-align: left;
}
.business-bet {
    display: flex;
    justify-content: space-between;
    padding-top: 1rem;
}
.bm-left {
    text-align: left;
}
.bm-left .left {
    color: #02bc6c;
}
.bm-user {
    font-size: 0.8rem;
}
.bm-room {
    margin: 10px 0;
    font-size: 0.8rem;
}
.bm-user span {
    color: #888888;
}
.bm-right-edu {
    font-size: 0.8rem;
    color: #888888;
}
.bm-time,
.bm-price {
    font-size: 0.8rem;
    color: #989898;
    padding-top: 0.6rem;
}
.bm-price {
    font-size: 0.9rem;
}
.bm-right {
    text-align: right;
}
.bmg-icon {
    width: 0.8rem;
    vertical-align: text-top;
}

/*按钮*/
.btn-group {
    text-align: right;
    padding-top: 1rem;
}
.business-btn {
    text-align: center;
    /* border: 1px solid #cecece; */
    color: #000;
    display: inline-block;
    font-size: 0.7rem;
    margin-left: 0.5rem;
    padding: 0.4rem 1rem;
    border-radius: 20px;
}
.cell-btn-none {
    color: #888888;
    border: 0.01rem solid #ececec;
}
.cell-btn-color {
    background: #ff7d41;
    color: #fff;
}
</style>



